:root {
  --cube-width: 100px;
}

.wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 300px;
  background: #000;
  overflow: hidden;
}

.container {
  position: relative;
  width: 100%;
  transform: rotate(-35deg);
}

.box {
  position: relative;
  left: -100px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: calc(100% + var(--cube-width) * 2);
  height: var(--cube-width);
  -webkit-box-reflect: below 1px linear-gradient(transparent, #0004);
  animation: animateSurface 1.5s ease-in-out infinite;
}

.cube {
  position: relative;
  width: var(--cube-width);
  height: var(--cube-width);
  background: #03e9f4;
  box-shadow: 0 0 5px rgba(3, 233, 244, 1), 0 0 25px rgba(3, 233, 244, 1),
    0 0 50px rgba(3, 233, 244, 1), 0 0 100px rgba(3, 233, 244, 1),
    0 0 200px rgba(3, 233, 244, 0.5), 0 0 300px rgba(3, 233, 244, 0.5);
  transform-origin: bottom right;
  animation: animate 1.5s ease-in-out infinite;
}

@keyframes animate {
  0% {
    transform: rotate(0deg);
  }
  60% {
    transform: rotate(90deg);
  }
  65% {
    transform: rotate(85deg);
  }
  70% {
    transform: rotate(90deg);
  }
  75% {
    transform: rotate(87.5deg);
  }
  80%,
  100% {
    transform: rotate(90deg);
  }
}

@keyframes animateSurface {
  0% {
    transform: translateX(0px);
  }
  100% {
    transform: translateX(-100px);
  }
}
