.wrapper {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 300px;
  background-color: #0e0e0e;
}

.text {
	  position: relative;
  width: 100%;
  text-align: center;
	filter: url(#fire);
}

.text > h2 {
  position: relative;
  color: #ffeb3b;
  font-weight: 600;
  font-size: 16em;
  -webkit-box-reflect: below -120px 
	linear-gradient(transparent, #0008);
  text-shadow: 0 0 10px #ff8c3b,
	 0 0 40px #ff8c3b,
	  0 0 80px #ff8c3b,
	0 0 160px #ff8c3b,
	 0 0 240px #ff8c3b;
}

.svg {
	width: 0;
	height: 0;
}